import { LoadingController } from "@ionic/angular";
import { FormGroup, FormControl } from "@angular/forms";
import { PlansService } from "./../../services/plans.service";
import { DietService } from "./../../services/diet.service";
import { Router } from "@angular/router";
import { UserService } from "./../../services/user.service";
import { Component, OnInit } from "@angular/core";

@Component({
  selector: "app-diet-pre-training",
  templateUrl: "./diet-pre-training.page.html",
  styleUrls: ["./diet-pre-training.page.scss"],
})
export class DietPreTrainingPage implements OnInit {
  mesAtual;
  preTreino;
  programa;
  slideOpts = {
    initialSlide: 1,
    speed: 400,
  };

  constructor(
    private userSrv: UserService,
    private router: Router,
    private dietSrv: DietService,
    private planSrv: PlansService,
    private loadCtrl: LoadingController
  ) {}

  ngOnInit() {
    this.loadDiet();
  }

  async loadDiet() {
    this.loadInfo();
    const loading = await this.loadCtrl.create({ message: "Carregando" });

    try {
      const monthPlan = await this.planSrv.getMonthPlan().toPromise();
      const dia = localStorage.getItem("dia");
      const dadosDieta = {
        mes: monthPlan.mes_atual,
        dia: dia,
        refeicao: 1,
      };

      const dietData = await this.dietSrv
        .getDietUserDay(dadosDieta)
        .toPromise();

      loading.present();
      this.preTreino = dietData.dados;
      console.log(this.preTreino);
    } catch (error) {
      console.error("Erro ao carregar a dieta:", error);
    } finally {
      loading.dismiss();
    }
  }

  loadInfo() {
    this.planSrv.getMonthPlan().subscribe((data) => {
      console.log(data);
      this.mesAtual = data.mes_atual;
    });
    this.userSrv.getUserPlan().subscribe((response) => {
      this.programa = response.programa;
      console.log(this.programa);
    });
  }
  backToSnackWeek() {
    this.router.navigate(["snack-week"]);
  }
}
